@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
* {
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #fff;
}
::-webkit-scrollbar-thumb {
  background: #a55eea;
  height: 15%;
  border-radius: 5px;
}

.header-logo{
    width: 130px;
}

.header-menu-list{
    color: #474747 !important;
    font-weight: 500;
}

.log-in-btn{
    background: #F63E7B;
    padding: 8px 40px !important;
    border-radius: 6px;
    color: #fff !important;
    font-weight: 600;
}

@media (max-width:600px) {
    .header-logo{
        width: 70px;
    }
}
.single-order-img {
  width: 85px;
}
.single-order-title {
  font-size: 25px;
  font-weight: 600;
  color: #111430;
  padding: 30px 0 12px;
}

.single-order-description {
  color: #606268;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.6;
}
.single-order-box {
  border-radius: 20px;
}
.order-review-button {
  display: inline-block;
  background: #ffe3e3;
  padding: 12px 35px;
  border-radius: 8px;
  color: #ff5454;
  font-weight: 600;
  font-size: 18px;
}

.make-admin-box {
  width: 40%;
  margin: 0 auto;
}

.bookservice-form-box {
  width: 100%;
  margin: 20px auto;
}

.router-item {
  background: rgba(221, 221, 221, 0.76);
  min-height: 100vh;
  border-radius: 10px 0 0 0;
}
.dashboard-list {
  padding: 12px 0px;
}
.dashboard-list a {
  text-decoration: none;
  font-size: 17px;
  font-weight: 500;
  color: #878787;
}
.dashboard-list a:hover {
  color: #f63e7b;
}

.dashboard-list i {
  margin-right: 10px;
}
.dashboard-list-main {
  padding: 0 55px;
  z-index: 1000px;
}

.bookservice-form-box input {
  width: 100%;
  padding: 5px 10px;
  margin: 8px 0;
  border-radius: 5px;
  outline: 0;
  border: 1.5px solid #111430;
  color: #111430;
  font-weight: 500;
}
.bookservice-form-box input:focus {
  border-color: #f63e7b;
}
.all-head-title {
  font-size: 45px;
  font-weight: 600;
  color: #111430;
}

.loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f8f8f8;
}
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}

.common-button{
    text-decoration: none;
    background: #F63E7B;
    padding: 11px 37px;
    border-radius: 6px;
    color: #fff;
    font-weight: 500;
    font-size: 17px;
    display: inline-block;
    margin:20px 0;
}

.common-button:hover{
    background: #f39c12 ;
    color: #fff;
}

.banner-main-box{
    background: #FFF8F5;
}

.banner-head-title{
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 700;
    color: #111430;
    margin-bottom:22px;
}

.banner-para-text{
    font-size: 16px;
    font-weight: 500;
    color: #676767;
    line-height: 1.6;
}
.main-facial-box{
    background: #FFF8F5;
}

.facial-text .facial-title{
    font-size: 50px;
    font-weight: 600;
    color: #2D2D2D;
    margin-bottom: 30px;
}

.facial-text .facial-title span{
    color: #F63E7B;
}

.facial-description{
    color: #7F7B7A;
    line-height: 1.6;
    margin-bottom: 25px;
}

.facial-review h1{
    font-size: 60px;
    font-weight: 800;
    color: #F63E7B;
    margin-bottom: 30px;
}
.facial-review h4{
    font-size: 18px;
    font-weight: 600;
}
.contact-form-main {
  background: #fff8f5;
}
.contact-form-box {
  width: 40%;
  margin: 0 auto;
}
.contact-input,
.contact-textarea {
  padding: 12px 15px;
  margin: 15px 0;
  border-radius: 5px;
  outline: none;
  border: none;
  color: #888888;
}
.contact-textarea {
  resize: none;
}

.main-title {
  font-size: 45px;
  font-weight: 700;
  color: #111430;
}

.main-title span {
  color: #f63e7b;
}

.single-services {
  transition: 0.3s;
}

.single-services img {
  width: 70px;
}

.single-services:hover {
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.5);
}

.single-services .service-title {
  font-size: 26px;
  color: #111430;
  font-weight: 700;
}

.single-services .service-price {
  color: #f63e7b;
  font-weight: 700;
}

.service-descrip {
  color: #4d4d4dd3;
  font-weight: 500;
}

.buy-now-btn {
  text-decoration: none;
  background: #f63e7b;
  display: inline-block;
  color: #fff;
  padding: 8px 22px;
  border-radius: 24px 0 24px 0;
  font-weight: 600;
}

.testi-img {
  width: 70px;
}
.testi-name {
  font-size: 20px;
}
.testi-address {
  font-size: 14px;
}
.main-testi-box {
  transition: 0.3s;
}

.main-testi-box:hover {
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.2);
}

.log-in-form-box {
  width: 35%;
  margin: 0 auto;
}
.login-input {
  border: none;
  outline: none !important;
}
.form-control:focus {
  box-shadow: none;
}
.input-form-border {
  border-bottom: 1.5px solid #c4c4c4;
  margin: 30px 0;
}
.form-floating > label {
  padding: 1rem 0rem;
  color: rgba(0, 0, 0, 0.808);
  font-weight: 500;
  font-size: 20px;
}

.create-para {
  color: #000000;
  font-weight: 500;
  font-size: 18px;
}

.create-link {
  color: #f63e7b;
}

.google-img {
  width: 50px;
  margin-right: 60px;
}

.google-button {
  width: 60%;
  border: 1px solid #c4c4c4;
  padding: 6px 15px;
  border-radius: 35px;
  margin: 40px auto;
}

.google-button:hover {
  background: #c4c4c4;
}

.google-button h4 {
  font-size: 20px;
  color: #000000;
  margin-bottom: 0;
}

ul {
  margin-left: 0;
  padding-left: 0;
}

ul li {
  list-style: none;
}
.footer-main-box {
  background: #f63e7b;
  color: #fff;
}

.footer-social ul li {
  display: inline-block;
}
.footer-social ul li i {
  margin-right: 15px;
  font-size: 25px;
  display: inline-block;
}
.footer-social ul li i:hover {
  cursor: pointer;
}
.footer-title {
  font-size: 25px;
  font-weight: 500;
  margin-bottom: 22px;
}

.footer-link ul li {
  font-size: 16px;
  font-weight: 400;
  margin: 10px 0;
}

